* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  text-align: center;
  background-color: #f4f4f4;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

header {
  text-align: center;
  margin-bottom: 20px;
}

header h1 {
  color: #333;
  font-size: 2rem;
  font-weight: 700;
}

header select {
  color: #333;
  background-color: #fff;
  border: 2px solid #333;
  border-radius: 5px;
  width: 200px;
  margin-top: 10px;
  padding: 10px;
  font-size: 1rem;
}

header select:focus {
  border-color: #1c5d95;
  outline: none;
}

#summary {
  justify-content: space-around;
  margin-bottom: 30px;
  display: flex;
}

#summary section {
  background-color: #fff;
  border-radius: 10px;
  width: 30%;
  padding: 20px;
  box-shadow: 0 2px 8px #0000001a;
}

#summary h2 {
  color: #1c5d95;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

#summary div {
  color: #333;
  font-size: 2rem;
  font-weight: bold;
}

#entries {
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
  display: flex;
}

.entry-card {
  background-color: #fff;
  border-radius: 10px;
  width: 32%;
  margin-bottom: 20px;
  padding: 20px;
  position: relative;
  box-shadow: 0 2px 8px #0000001a;
}

.entry-card h2 {
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.entry-card button {
  color: #fff;
  cursor: pointer;
  background-color: #1c5d95;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 12px 24px;
  font-size: 1rem;
  transition: background-color .3s;
}

.entry-card button:hover, .entry-card button:focus {
  background-color: #154b7f;
}

.entry-card button:focus {
  outline: 2px solid #154b7f;
}

.entry-card form label {
  color: #555;
  margin-top: 15px;
  font-size: 1rem;
  display: block;
}

.entry-card form input, .entry-card form select {
  color: #333;
  background-color: #fff;
  border: 2px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-top: 8px;
  padding: 12px;
  font-size: 1rem;
}

.entry-card form input[type="number"] {
  appearance: textfield;
}

.entry-card form button {
  color: #fff;
  cursor: pointer;
  background-color: #1c5d95;
  border: none;
  border-radius: 5px;
  width: 100%;
  margin-top: 20px;
  padding: 12px 24px;
  font-size: 1.1rem;
  transition: background-color .3s;
}

.entry-card form button:hover, .entry-card form button:focus {
  background-color: #154b7f;
  outline: 2px solid #154b7f;
}

.entry {
  border-bottom: 1px solid #ccc;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.entry .eDel {
  cursor: pointer;
  color: #ab2020;
  font-weight: bold;
}

.entry .eTxt, .entry .eCat, .entry .eAmt, .entry .eSource {
  text-align: left;
  flex: 1;
}

.entry .eEdit {
  cursor: pointer;
  color: #1c5d95;
  font-weight: bold;
}

button:focus, input:focus, select:focus {
  outline: 3px solid #1c5d95;
}

@media (width <= 768px) {
  #summary {
    flex-direction: column;
    align-items: center;
  }

  #summary section {
    width: 80%;
    margin-bottom: 20px;
  }

  #entries {
    flex-direction: column;
    align-items: center;
  }

  .entry-card {
    width: 90%;
  }
}
